import React from "react";
import { Section } from "./general/style/common";
import MobileListItem from "./MobileListItem";
import { Loader } from "./general/style/style";
import ProductCompareTray from "./ProductCompareTray";
import NoProductMsg from "./NoProductMsg";
// import {toJS} from 'mobx';

class GadgetListing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modelLeft: null,
      modelRight: null,
    };
  }

  clearSelectedModels = () => {
    this.setState({
      modelLeft:null,
      modelRight:null
    })
  }

  itemSelectHandler = (id, itemName, image, product) => {
    try{
      if(id && this.state.modelLeft && this.state.modelLeft.id == id){
        this.setState({
          ...this.state,
          modelLeft: null,
        });
      }else if(id && this.state.modelRight && this.state.modelRight.id == id){
        this.setState({
          ...this.state,
          modelRight: null,
        });
      }else if(id && itemName && product && image && !this.state.modelLeft){
        this.setState({
          ...this.state,
          modelLeft:{id:id, name:itemName, image:image, product:product}
        });
      }else if(id && itemName && product && image && !this.state.modelRight){
        this.setState({
          ...this.state,
          modelRight:{id:id, name:itemName, image:image, product:product}
        });
      }
    }catch(err){
      console.log(err);
    }
  };

  nextPageInProgress = false;

  componentDidMount() {
    if (typeof (this.props.onScrollEnd) !== "undefined" && (Array.isArray(this.props.items) && this.props.items.length<5)) {
      const onScrollEndCB = async () => {
        if (this.nextPageInProgress || this.props.hidden) return;
        this.nextPageInProgress = true;
        this.props.updateLoading(true);
       // console.log("GadgetListing onScrollEnd Called");
        await this.props.onScrollEnd();
        this.props.updateLoading(false);
        this.nextPageInProgress = false;
    }
      typeof(loadFuncOnScrollEnd)==="object" && loadFuncOnScrollEnd.push(onScrollEndCB);
    }
  }

  render() {
    return (
      (!this.props.hidden ) ? (
        <>
          <Section padding="0 16px">
            {Array.isArray(this.props.items) &&
              this.props.items.map((el, index) => {
                return (
                  <MobileListItem
                    data={el}
                    key={index}
                    onClick={this.itemSelectHandler}
                    modelLeft={this.state.modelLeft}
                    modelRight={this.state.modelRight}
                  />
                );
              })}
          </Section>
          {this.props.loading && <Loader />}
          {!Array.isArray(this.props.items) && !this.props.loading && <NoProductMsg heading="Oops!" subHeading="Sorry, No Product found. Please choose different filters"/>}
          {(this.state.modelLeft || this.state.modelRight ) ? (
            <ProductCompareTray modelLeft={this.state.modelLeft} modelRight={this.state.modelRight} onClick={this.itemSelectHandler} clearSelectedModels={this.clearSelectedModels} />
          ) : (
            ""
          )}
        </>
      ):''
    );
  }
}

export default GadgetListing;






